import meter1 from "../assets/resources/meter1.svg";
import meter2 from "../assets/resources/meter2.svg";
import meter3 from "../assets/resources/meter3.svg";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import arrow1 from "../assets/resources/arrow1.svg";
import arrow2 from "../assets/resources/arrow2.svg";
import colorSharp from "../assets/resources/color-sharp.png";
import {
  Box,
  Center,
  Flex,
  Heading,
  SimpleGrid,
  Spacer,
} from "@chakra-ui/react";

export default function Github(){
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5,
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
    },
  };

  return (
    <section className="skill" id="skills">
      <div className="container">
        <div className="row">
          <div className="col-12">
            <div className="skill-bx wow zoomIn">
              <h2>My Github Contributions</h2>
              <Center>
              <GitHubCalendar username="ravipandeydu" />
              </Center>
              
            </div>
          </div>
        </div>
      </div>
      <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  );
};
